import uniIcon from '@/components/rate/font-rate/uni-icon';
export default {
	name: 'UniRate',
	components: {
		uniIcon
	},
	props: {
		isFill: {
			// 星星的类型，是否镂空
			type: Boolean,
			default: true
		},
		// 星星的颜色
		color: {
			type: String,
			default: '#CB003C'
		},
		// 星星选中状态颜色
		activeColor: {
			type: String,
			default: '#CB003C'
		},
		// 星星的大小
		size: {
			type: [Number, String],
			default: 24
		},
		// 当前评分
		value: {
			type: [Number, String],
			default: 0
		},
		// 最大评分
		max: {
			type: [Number, String],
			default: 5
		},
		// 星星的间距
		margin: {
			type: [Number, String],
			default: 0
		},
		// 是否可点击
		disabled: {
			type: Boolean,
			default: false
		},
		//是否有边框
		border:{
			type:String,
			default:''
		}
	},
	data() {
		return {
			valueSync: ''
		};
	},
	computed: {
		stars() {
			// const value = Number(this.valueSync) ? Number(this.valueSync) : 0
			const value = Number(this.value) ? Number(this.value) : 0;
			const starList = [];
			const floorValue = Math.floor(value);
			const ceilValue = Math.ceil(value);
			for (let i = 0; i < this.max; i++) {
				if (floorValue > i) {
					starList.push({
						activeWitch: '100%'
					});
				} else if (ceilValue - 1 === i) {
					starList.push({
						activeWitch: (value - floorValue) * 100 + '%'
					});
				} else {
					starList.push({
						activeWitch: '0'
					});
				}
			}
			return starList;
		}
	},
	created() {
		this.valueSync = this.value;
		console.log('///this.：：', this.value);
	},
	methods: {
		_onClick(index) {
			if (this.disabled) {
				return;
			}
			this.valueSync = index + 1;
			this.$emit('change', {
				value: this.valueSync
			});
		}
	}
};
